"use strict";import{Wordlist}from"./wordlist";const words="";let wordlist=null;function loadWords(lang){if(null==wordlist&&(wordlist=words.replace(/([A-Z])/g," $1").toLowerCase().substring(1).split(" "),"0x3c8acc1e7b08d8e76f9fda015ef48dc8c710a73cb7e0f77b2c18a9b5a7adde60"!==Wordlist.check(lang)))throw wordlist=null,new Error("BIP39 Wordlist for en (English) FAILED")}let LangEn=class extends Wordlist{getWord(index){return loadWords(this),wordlist[index]}getWordIndex(word){return loadWords(this),wordlist.indexOf(word)}constructor(){super("en")}};const langEn=new LangEn;Wordlist.register(langEn);export{langEn};